import React, { Component } from 'react'
import { connect } from 'react-redux';
import './Searchbar.css'
import * as actionCreators from '../../store/actions/actionCreators'
import MovieResult from '../../components/MovieResult/MovieResult'
import { withRouter } from 'react-router-dom'

class Searchbar extends Component {

    state = {
        query: ""
    }

    handleKeyUp = event => {
        const query = event.target.value
        this.setState({ query })
        this.props.onFetchByQuery(query)
    }

    handleClick = (id) => {
        this.setState({ query: "" })
        this.refs.input.value = "";
        if (/movie/.test(this.props.location.pathname)) {
            this.props.onFetchMovie(id)
        }
    }

    render() {
        const resultsContainerClasses = ['results-container', this.state.query.length ? 'visible' : '']
        let result = null
        const { movies } = this.props

        if (this.props.error) {
            result = (
                <div className="result-error">
                    Unable to fetch movies
                </div>
            )
        }

        if (movies.length) {
            result = movies.map(movie => {
                return <MovieResult key={movie.id} {...movie} clicked={() => this.handleClick(movie.id)} />
            })
        }  
        
        if (this.state.query.length && !movies.length) {
            result = (
                <div className="no-results">
                    No movies found
                </div>
            )
        }
        
        return (
            <div className="search-movies-container">
                <label>
                    <span>Search for movies by title</span>
                    <input placeholder="Please insert query" ref="input" onKeyUp={this.handleKeyUp} type="text"/>
                </label>
                <div ref="resultsContainer" className={resultsContainerClasses.join(' ')}>
                    <div className="content">
                        {result}
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => {
    return {
        movies: state.movieByQueryReducer.movies,
        error: state.movieByQueryReducer.error
    }
}

const mapDispatchToProps = dispatch => {
    return {
        onFetchByQuery: query => dispatch(actionCreators.fetchByQuery(query)),
        onFetchMovie: id => dispatch(actionCreators.fetchMovie(id))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Searchbar))


